/**=====================
    About us Page css start
==========================**/

// sitemap page //
.sitemap_page {
  .title {
    color: var(--theme-color);
    text-transform: capitalize;
    font-weight: 700;
  }

  ul {
    padding-left: 10px;

    li {
      display: block;
      margin-bottom: 10px;

      a {
        color: $dark-font;
        font-weight: 500;
        font-size: 16px;
        transition: all 0.5s ease;
        text-transform: capitalize;

        i {
          font-size: 10px;
        }

        &:hover {
          color: var(--theme-color);
          transition: all 0.5s ease;
        }
      }
    }

    ul {
      padding-left: 15px;
      margin-top: 5px;

      li {
        margin-bottom: 0;
        line-height: 24px;
        font-size: 20px;

        a {
          font-weight: 400;
          color: $grey;
          font-size: 16px;
        }
      }
    }
  }

  .row {
    > div {
      padding-top: 15px;
      padding-bottom: 15px;

      &:nth-child(odd) {
        background-color: $top-header;
      }
    }
  }
}

// profile page //
.select_input {
  select {
    padding: 15px 20px;
    margin-bottom: 30px;
  }
}

// coming soon //
.template-password {
  background: url("../images/coming-soon.jpg");
  background-size: cover;
  height: 100vh;

  #container {
    height: 100vh;
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;

    > div {
      width: 100%;
    }

    #login {
      width: 100%;
      margin-bottom: 120px;

      h2 {
        color: var(--theme-color);
        font-size: 30px;
        font-weight: 400;
      }

      .theme-form {
        h3 {
          color: $header-font;
          font-size: 20px;
        }

        input {
          border-color: $round-border;
          font-size: 12px;
          padding: 17px 25px;
          margin-bottom: 15px;
          height: inherit;
        }

        input {
          &:focus {
            box-shadow: none;
          }
        }
      }
    }

    #powered {
      position: absolute;
      bottom: 5px;
      left: 0;
      right: 0;
    }

    #owner {
      a {
        color: var(--theme-color);
      }
    }
  }
}

.review-page {
  .comment-section {
    border-bottom: none !important;

    li {
      &:first-child {
        border-top: none;
        padding-top: 0;
      }

      &:last-child {
        padding-bottom: 0;
      }
    }
  }

  .comnt-sec {
    margin-top: 10px;

    li {
      margin-right: 15px;
      padding-bottom: 0 !important;
      padding-top: 0 !important;
      border-top: none !important;

      a {
        color: $grey;
        font-size: 16px;

        i {
          margin-right: 5px;
        }
      }
    }
  }
}

// error section //
.error-section {
  padding: 130px 0 140px;
  text-align: center;

  h1 {
    font-size: 200px;
    font-weight: 900;
    margin: 0;
    line-height: 0.8;
    color: $font-grey;
    margin-top: -11px;
  }

  h2 {
    color: $font-grey;
    font-weight: 500;
    letter-spacing: 3px;
    margin: 40px 0;
  }
}

// breadcrumb section //
.breadcrumb-section {
  border-top: 1px solid #eee;
  padding-top: 14px;

  .page-title {
    display: flex;
    height: 100%;
    align-items: center;

    h2 {
      font-size: 16px;
      margin-bottom: 0;
    }
  }

  .breadcrumb {
    background-color: transparent;
    text-transform: uppercase;
    font-size: 14px;
    color: $grey2;
    font-weight: 600;
    margin: 0;
    padding: 0;

    a {
      color: $grey2;
      font-weight: 600;
    }
  }
}

// about page section //
.about-head {
  margin-bottom: 20px;
  align-items: center;
  display: flex;
  height: 100%;

  h2 {
    color: var(--theme-color);
    margin-bottom: 0;
  }
}

.about-page {
  p {
    margin-bottom: -7px;
  }

  .banner-section {
    border-radius: 5px;
    overflow: hidden;
  }

  h4 {
    margin-bottom: 10px;
    margin-top: 30px;
    text-transform: capitalize;
    font-weight: 600;
    line-height: 1.3;
  }

  p {
    line-height: 2;
    letter-spacing: 0.03em;
  }
}

.testimonial {
  background-color: $grey-about;

  .testimonial-slider {
    .media {
      img {
        border-radius: 100%;
        border: 1px solid $round-border;
        padding: 5px;
        height: 130px;
        width: 130px;
      }

      h5 {
        font-weight: 700;
        color: var(--theme-color);
        margin-bottom: 0;
        margin-top: 7px;
      }

      h6 {
        margin-bottom: -4px;
      }

      .media-body {
        padding: 30px 75px 30px 30px;

        p {
          line-height: 2;
          letter-spacing: 0.03em;
        }
      }
    }

    .slick-track {
      .slick-slide {
        &:nth-child(even) {
          .media {
            border-left: 1px solid $round-border;
            padding-left: 50px;
          }
        }
      }
    }
  }
}

.team {
  position: relative;

  img {
    margin: 0 auto;
    padding: 0 10px;
  }

  .slick-prev,
  .slick-next {
    position: absolute;
    top: -45px;

    &:before {
      font: normal normal normal 30px/1 $font-awesome;
      color: $dark-font;
      opacity: 0.6;
    }
  }

  .slick-prev {
    left: unset;
    right: 20px;

    &:before {
      content: "\f104";
    }
  }

  .slick-next {
    right: 0;

    &:before {
      content: "\f105";
    }
  }

  h2 {
    color: $dark-font;
    padding-bottom: 15px;
    border-bottom: 1px solid $border;
    margin-bottom: 10px;
  }

  h4,
  h6 {
    text-align: center;
  }

  h4 {
    color: var(--theme-color);
    font-weight: 700;
    margin-top: 15px;
    margin-bottom: 5px;
  }

  h6 {
    margin-bottom: 0;
  }
}

// category page //
.collection-filter-block {
  margin-bottom: 30px;
  padding: 0 30px;
  border: 1px solid lighten($grey-lighter, 86.5%);
  background-color: $grey-lighter;

  .product-service {
    padding: 30px 0;

    .media {
      padding-bottom: 20px;
      margin-bottom: 20px;

      svg {
        height: 40px;
        width: 40px;
        fill: var(--theme-color);

        path {
          fill: var(--theme-color);
        }
      }

      .media-body {
        padding-left: 10px;

        h4 {
          margin-bottom: 8px;
          font-size: 14px;
          font-weight: 600;
        }

        p {
          margin-bottom: 0;
          text-transform: capitalize;
        }
      }

      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

.collection-collapse-block {
  &.open {
    margin-bottom: 12px;
  }

  padding-bottom: 24px;

  .collapse-block-title {
    position: relative;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 16px;
    color: lighten($black, 26.5%);
    font-weight: 600;
    margin: 30px 0 0 0;

    &:after {
      position: absolute;
      display: block;
      top: 0;
      right: -3px;
      content: "\f0d7";
      font-family: $font-awesome;
      color: lighten($black, 26.5%);
    }
  }

  .collection-collapse-block-content {
    .color-selector {
      margin-top: 30px;
    }

    .collection-brand-filter {
      .category-list {
        li {
          display: flex;
          font-size: 16px;
          text-transform: uppercase;
          line-height: 18px;

          a {
            color: $grey;
            padding-bottom: 10px;
          }

          &:last-child {
            a {
              padding-bottom: 0;
            }
          }

          &:first-child {
            margin-top: 30px;
          }
        }
      }

      .collection-filter-checkbox {
        margin-bottom: 12px;

        &:first-child {
          margin-top: 30px;
        }

        &:last-child {
          margin-bottom: 0;
        }

        label {
          text-transform: uppercase;
          cursor: pointer;
          font-size: 12px;
          color: lighten($black, 46.5%);
          font-weight: 600;
          letter-spacing: 0.5px;
          padding-left: 10px;

          &:before {
            top: 1px;
            height: 15px;
            width: 15px;
            background: $white;
            border: 1px solid lighten($black, 46.5%);
          }

          &:after {
            top: 1px;
            height: 15px;
            width: 15px;
            background-size: 65%;
          }
        }

        .form-check-input {
          &:checked {
            ~ .form-check-label {
              &:after {
                background-image: url("data:image/svg+xml;charset=utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'><path fill='%23000' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/></svg>");
              }
            }
          }
        }
      }
    }
  }
}

.collection-filter {
  .theme-card {
    .offer-slider {
      img {
        padding: 15px 15px 15px 0;
      }
    }
  }
}

.color-selector {
  ul {
    li {
      height: 30px;
      width: 30px;
      border-radius: 50%;
      margin-right: 5px;
      margin-bottom: 10px;
      cursor: pointer;
      border: 1px solid $grey4;
    }

    .color-1 {
      background-color: white;
    }

    .color-2 {
      background-color: grey;
    }

    .color-3 {
      background-color: $color-red;
    }

    .color-4 {
      background-color: purple;
    }

    .color-5 {
      background-color: blue;
    }

    .color-6 {
      background-color: yellow;
    }

    .color-7 {
      background-color: green;
    }

    li.active {
      border: 1px solid $black;
      position: relative;

      &:after {
        content: "";
        background-image: url("data:image/svg+xml;charset=utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'><path fill='%23000' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/></svg>");
        top: 9px;
        right: 4px;
        height: 15px;
        width: 15px;
        background-size: 70%;
        background-repeat: no-repeat;
        position: absolute;
        opacity: 0.4;
      }
    }
  }
}

.color-w-name {
  margin-top: 30px;

  ul {
    li {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      text-transform: uppercase;
      font-size: 12px;
      color: $grey;
      font-weight: 600;
      letter-spacing: 0.5px;
      cursor: pointer;

      span {
        display: block;
        height: 26px;
        width: 26px;
        border-radius: 3px;
        margin-right: 5px;
        margin-bottom: 0;
        border: 1px solid $grey;
        margin-right: 10px;
      }
    }

    .color-1 {
      background-color: white;
    }

    .color-2 {
      background-color: grey;
    }

    .color-3 {
      background-color: red;
    }

    .color-4 {
      background-color: purple;
    }

    .color-5 {
      background-color: blue;
    }

    .color-6 {
      background-color: yellow;
    }

    .color-7 {
      background-color: green;
    }

    li.active {
      span {
        border: 1px solid $grey;
        position: relative;

        &:after {
          content: "";
          background-image: url("data:image/svg+xml;charset=utf8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'><path fill='%23000' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/></svg>");
          top: 7px;
          right: 3px;
          height: 15px;
          width: 15px;
          background-size: 70%;
          background-repeat: no-repeat;
          position: absolute;
        }
      }
    }
  }
}

.collection-sidebar-banner {
  margin-top: 30px;

  img {
    width: 100%;
  }
}

.top-banner-wrapper {
  .top-banner-content {
    h4 {
      font-weight: 600;
      color: lighten($black, 26.5%);
      margin-bottom: 15px;
    }

    h5 {
      font-size: 16px;
      font-weight: 600;
      color: lighten($black, 26.5%);
      letter-spacing: 1px;
      line-height: 1.2;
      margin-bottom: 10px;
    }

    p {
      line-height: 1.5;
      color: lighten($black, 46.5%);
      margin-bottom: 0;
      font-size: 16px;
      letter-spacing: 0.05em;
    }
  }
}

.product-wrapper-grid.list-view {
  .product-wrap {
    .product-info {
      text-align: left;
      align-self: center;
      padding-left: 15px;
    }
  }

  .product-box {
    display: flex;
    padding-bottom: 0;

    .img-wrapper,
    .img-block {
      width: 25%;
    }

    .product-detail {
      padding-left: 15px;
      align-self: center;
      text-align: left !important;

      .rating {
        margin-top: 0;
      }

      p {
        display: block !important;
        margin-bottom: 5px;
        line-height: 23px;
        font-size: 16px;
      }

      .color-variant {
        padding-top: 10px;
      }

      h6 {
        font-weight: 700;
      }
    }
  }
}

.collection-product-wrapper {
  .product-top-filter {
    border-bottom: 1px solid lighten($black, 86.5%);
    border-top: 1px solid lighten($black, 86.5%);

    .product-filter-content,
    .popup-filter {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .search-count,
      .sidebar-popup {
        border-right: 1px solid lighten($black, 86.5%);
        padding: 20px;
        text-transform: capitalize;
        width: 35%;

        h5 {
          margin-bottom: 0;
        }
      }

      .collection-view {
        padding: 20px;
        width: 10%;

        ul {
          li {
            height: 100%;
            vertical-align: middle;
            cursor: pointer;

            &:first-child {
              margin-right: 14px;
            }
          }
        }
      }

      .collection-grid-view {
        padding: 20px;
        width: 20%;
        border-right: 1px solid lighten($black, 86.5%);

        ul {
          display: flex;
          justify-content: flex-end;

          li {
            margin-left: 10px;

            img {
              height: 18px;
              cursor: pointer;
            }
          }
        }
      }

      .product-page-per-view,
      .product-page-filter {
        position: relative;
        width: 20%;

        select {
          border: 0;
          padding: 20px;
          border-right: 1px solid lighten($black, 86.5%);
          width: 100%;
          -webkit-appearance: none;
          -moz-appearance: none;
          cursor: pointer;
          background: url(../images/dropdown.png) no-repeat 95%;

          &:focus {
            outline: unset;
          }

          option {
            padding: 20px 0;
          }
        }
      }

      .product-page-filter {
        width: 15%;

        &:before {
          right: 10px;
        }

        select {
          border-right: none;
        }
      }
    }

    .popup-filter {
      justify-content: unset;

      ::-webkit-scrollbar {
        width: 3px;
      }

      ::-webkit-scrollbar-track {
        background: $round-border;
      }

      ::-webkit-scrollbar-thumb {
        background: $grey5;
      }

      ::-webkit-scrollbar-thumb:hover {
        background: $grey2;
      }

      .sidebar-popup {
        position: relative;
        cursor: pointer;
        width: 20%;
        background: url(../images/dropdown.png) no-repeat 95%;

        a {
          font-size: 16px;
          color: $dark-font;
        }

        .collection-filter {
          background-color: $top-header;
          width: 270px;
          height: 600px;
          overflow-y: scroll;

          .collection-filter-block {
            border: none;
          }

          .theme-card {
            padding: 0 30px;
          }

          .collection-sidebar-banner {
            padding: 0 30px;

            img {
              width: 100%;
            }
          }
        }
      }

      .open-popup {
        display: none;
        position: absolute;
        z-index: 9;
        top: 75px;
        left: 0;
        border: 1px solid $border-grey;
        box-shadow: 0 0 5px $round-border;
        min-width: 330px;

        &.open {
          display: block;
        }

        .collection-filter-block {
          margin-bottom: 0;
        }
      }

      .product-page-per-view,
      .product-page-filter {
        width: 20%;

        select {
          padding-right: 45px;
          font-size: 16px;
        }

        &:before {
          right: 35px;
        }
      }

      .collection-grid-view {
        padding: 20px;
      }

      .collection-view {
        width: 20%;
      }

      .product-page-filter {
        &:before {
          right: 25px !important;
        }
      }
    }
  }

  .product-wrapper-grid {
    .product-box {
      margin-top: 50px;

      .product-detail {
        p {
          display: none;
        }
      }
    }

    .product-five {
      flex: 0 0 20%;
      max-width: 20%;
      position: relative;
      width: 100%;
      min-height: 1px;
      padding-right: 15px;
      padding-left: 15px;
    }

    .col-lg-2 {
      .product-box {
        .product-detail {
          h6 {
            font-size: 13px;
          }

          h4 {
            font-size: 18px;
          }

          .rating {
            i {
              padding-right: 0;
            }
          }
        }

        .img-wrapper {
          .lable-block {
            .lable3 {
              padding: 8px 4px;
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}

.filter-main-btn {
  display: none;
  margin-bottom: 20px;
  cursor: pointer;

  span {
    width: auto;
    border-radius: 0;
    font-size: 18px;
    background-color: var(--theme-color);
    padding: 5px 16px;
    color: $white;
    margin-bottom: 20px;
    display: inline-block;
    text-transform: uppercase;
  }
}

.collection-mobile-back {
  padding: 15px 0;
  border-bottom: 1px solid lighten($black, 86.5%);
  display: none;

  span {
    text-transform: uppercase;
    font-size: 14px;
    color: $black;
    cursor: pointer;

    i {
      font-size: 20px;
      font-weight: bold;
      margin-right: 5px;
    }
  }
}

.product-pagination {
  border-bottom: 1px solid lighten($black, 86.5%);
  border-top: 1px solid lighten($black, 86.5%);
  margin: 50px 0;

  .pagination {
    border-radius: 0;

    .page-item {
      border-radius: 0;

      a {
        padding: 18px;
        border-bottom: 0;
        border-top: 0;
        color: lighten($black, 46.5%);
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
      }

      &:last-child {
        .page-link {
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
      }
    }

    .page-item.active {
      a {
        color: var(--theme-color);
        background-color: $white;
        border-color: lighten($black, 86.5%);
      }
    }
  }

  .product-search-count-bottom {
    justify-content: flex-end;
    display: flex;
    align-items: center;
    height: 100%;
    border-left: 1px solid lighten($black, 86.5%);
    border-right: 1px solid lighten($black, 86.5%);
    padding-right: 15px;

    h5 {
      color: lighten($black, 46.5%);
      font-size: 14px;
      margin-bottom: 0;
      padding: 5px 0;
    }
  }
}

.bundle {
  margin-top: 10px;

  .bundle_img {
    display: flex;

    .img-box {
      img {
        max-width: 115px;
        border: 1px solid $round-border;
        padding: 2px;
      }
    }

    .plus {
      display: flex;
      align-items: center;
      padding: 0 7px;
      font-size: 22px;
    }
  }

  .bundle_detail {
    margin-top: 15px;

    .price_product {
      color: $black;
      padding-left: 3px;
      font-weight: 700;
    }
  }
}

.theme_checkbox {
  label {
    display: block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 14px;
    user-select: none;
    text-transform: capitalize;
    color: $font-color;

    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 0;
      width: 0;

      &:checked {
        ~ .checkmark {
          &:after {
            display: block;
          }
        }
      }
    }

    .checkmark {
      position: absolute;
      top: 0;
      left: 0;
      height: 20px;
      width: 20px;
      background-color: $white;
      border: 1px solid $round-border;

      &:after {
        content: "";
        position: absolute;
        display: none;
        left: 7px;
        top: 3px;
        width: 5px;
        height: 10px;
        border: solid $black;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg);
      }
    }
  }
}

.sticky-top-cls {
  position: sticky;
  top: 20px;
  z-index: 1;
}

.mordern-box-margin {
  margin-bottom: -40px;
}

.mordern-box {
  padding: 25px 25px;
  background-color: $top-header;
  margin-top: 94px;
  margin-bottom: 40px;

  .absolute-img {
    img {
      margin-left: 30px;
      margin-top: -120px;
      transition: all 0.5s ease;
    }
  }

  .mordern-content {
    margin-top: 14px;

    h6 {
      font-size: 16px;
      margin-bottom: 2px;
      font-weight: 600;
      color: $dark-font;
    }

    p {
      line-height: 20px;
      letter-spacing: 0.05em;
      margin-bottom: 14px;
    }

    .mordern-bottom {
      display: flex;

      .right {
        margin-left: auto;

        .add-extent {
          position: relative;

          .animated-btn {
            cursor: pointer;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 100%;
            background-color: #e8e8e8;
            transition: all 0.5s ease;
            color: $dark-font;
          }

          .options {
            position: absolute;
            right: -6px;
            bottom: 50%;
            background-color: $white;
            padding: 10px 5px;
            border-radius: 20px;
            box-shadow: 0px 5px 7px 1px rgba($black, 0.09);
            height: 0;
            opacity: 0;
            visibility: hidden;

            li {
              width: 30px;
              height: 30px;
              border-radius: 100%;
              padding: 8px;
              display: flex;
              align-items: center;
              justify-content: center;
              cursor: pointer;

              button {
                border: none;
                background-color: transparent;
              }

              i {
                color: $grey6;
                font-size: 18px;
              }

              + li {
                margin-top: 6px;
              }
            }
          }

          &.show {
            i {
              transform: rotate(45deg);
              transition: all 0.5s ease;
            }

            .options {
              opacity: 1;
              visibility: visible;
              height: auto;
              bottom: 38px;
              transition: all 0.5s ease;
            }
          }
        }
      }
    }
  }
}

