.top-filter {
  position: absolute;
  z-index: 1;
  top: 80px;
  left: 0;
  width: 100%;
  display: none;

  ::-webkit-scrollbar {
    width: 4px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  .collection-collapse-block {
    padding-bottom: 0;

    .collapse-block-title {
      &:after {
        display: none;
      }
    }
  }

  .button_bottom {
