    max-height: 210px;
    overflow-y: auto;
    overflow-x: hidden;
  }
}

.filter-bottom-title {
  position: relative;
  cursor: pointer;

  &:after {
    position: absolute;
    display: block;
    top: 0;
    right: -3px;
    content: "\f0d7";
    font-family: $font-awesome;
    font-weight: 900;
    color: #969696;
  }
}

.sticky-bottom-cart {
  position: fixed;
  bottom: -200px;
  width: 100%;
  z-index: 9;
  background-color: white;
  padding: 15px;
  box-shadow: 0 0 100px rgba(0, 0, 0, 0.06);
  transition: all 0.5s ease;
  left: 50%;
  transform: translateX(-50%) scale(0.95);

  .qty-box {
    .input-group {
      justify-content: center;
      margin-bottom: 10px;
    }
  }

  .cart-content {
    display: flex;
    align-items: center;
  }

  .product-image {
    display: flex;
    align-items: center;

    img {
      width: 70px;
      height: 70px;
      object-fit: cover;
      object-position: top;
      background-color: #f8f8f8;
    }

    .content {
      margin-left: 12px;

      h5,
      h6 {
        margin-bottom: 0;
      }

      h5 {
        font-size: 18px;
        font-weight: 500;
      }

      h6 {
        font-size: 15px;
        color: $dark-font;

        del {
          margin-left: 7px;
        }

        span {
          color: var(--theme-color);
          margin-left: 5px;
        }
      }
    }
  }

  .selection-section {
    display: flex;
    align-items: center;
    margin: 0 auto;
    text-align: center;

    .form-control {
      background-color: $top-header;
      border: 1px solid $top-header;
      text-transform: uppercase;

      &:focus {
        box-shadow: none;
      }
