.stickyCart {
  .sticky-bottom-cart {
    bottom: 20px;
    transition: all 0.5s ease;
    transform: translateX(-50%) scale(1);
  }

  .recently-purchase {
    &.show {
      bottom: 110px;
    }
  }

  .tap-top {
    bottom: 110px;
  }
}

.product-center-page {
  .product-right.right-side {
