.custom-variations {
  ul {
    display: flex;
    margin-left: -10px;
    margin-right: -10px;

    li {
      display: flex;
      align-items: center;
      border: 1px solid $round-border;
      margin: 0 10px;
      padding: 5px;
      text-align: center;
      cursor: pointer;

      img {
        width: 50px;
        height: 70px;
        object-fit: cover;
        object-position: center;
      }

      .product-content {
        padding-left: 15px;
        padding-right: 25px;

        h6 {
          margin-bottom: 0;
          color: $dark-font;
          font-weight: 600;
          font-size: 15px;
        }

        span {
          color: #888;
          font-size: 15px;
        }
      }

      &.active {
        border-color: var(--theme-color);
      }
    }
  }
}

@keyframes buttons-shake {
  10%,
  90% {
    -webkit-transform: translate3d(-1px, 0, 0);
    transform: translate3d(-1px, 0, 0);
  }

  20%,
  80% {
    -webkit-transform: translate3d(2px, 0, 0);
    transform: translate3d(2px, 0, 0);
  }

  30%,
  50%,
  70% {
    -webkit-transform: translate3d(-2px, 0, 0);
    transform: translate3d(-2px, 0, 0);
  }

  40%,
  60% {
    -webkit-transform: translate3d(2px, 0, 0);
    transform: translate3d(2px, 0, 0);
  }
}

// 360 product view
.show-360 {
  #product360_view {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}

.image-360 {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 20px;
  z-index: 1;
}

.product-image-360 {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  cursor: pointer;

  .nav_bar {
    position: absolute;
    bottom: 40px;
    left: 50%;
    margin-left: -67.5px;
    z-index: 11;
    background-color: $white;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.15);

    a {
      display: inline-block;
      width: 45px;
      height: 45px;
      line-height: 48px;
      text-align: center;
      text-decoration: none;
      color: $font-grey;
    }
  }

  .product-images-item {
    display: none;
    list-style: none;
    margin: 0;
    padding: 0;

    img.previous-image {
      visibility: hidden;
    }

    img.current-image {
      visibility: visible;
    }

    img {
      position: absolute;
      top: 50%;
      vertical-align: middle;
      height: auto;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 100%;
    }
  }

  .spinner {
    background: $white;
    position: relative;
    z-index: 999;
    width: 800px;
    height: 90px;
    margin-top: 0 !important;
    display: inline-block;

    span {
      width: 60px;
      height: 60px;
      line-height: 60px;
      text-align: center;
      display: block;
      margin: 0 auto;
      color: $black;
      font-weight: bolder;
      background: rgba(255, 255, 255, 0.7);
      box-shadow: 0 1px 4px rgba(0, 0, 0, 0.15);
      border-radius: 50%;
      margin-top: 20px;
