      &.active {
        color: var(--theme-color);
        border-right: 2px solid var(--theme-color);

        a {
          color: var(--theme-color);
        }
      }

      &:before {
        display: inline-block;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        width: 30px;
        height: 30px;
        text-align: center;
        font-size: 18px;
        font-weight: 700;
        margin-top: 3px;
      }

      a {
        color: black;
      }

      &:hover {
        a {
          color: var(--theme-color);
          transition: all 0.5s ease;
        }

        color: var(--theme-color);
        transition: all 0.5s ease;
      }
