// Order-success //
.success-text {
  text-align: center;

  .star {
    position: absolute;
    animation: grow 3s infinite;
    fill: var(--theme-color);
    opacity: 0;

    &:nth-child(1) {
      @include size(12px);
      left: 12px;
      top: 16px;
    }

    &:nth-child(2) {
      @include size(18px);
      left: 168px;
      top: 84px;
    }

    &:nth-child(3) {
      @include size(10px);
      left: 32px;
      top: 162px;
    }

    &:nth-child(4) {
      @include size(20px);
      left: 82px;
      top: -12px;
    }

    &:nth-child(5) {
      @include size(14px);
      left: 125px;
      top: 162px;
    }

    &:nth-child(6) {
      @include size(10px);
      left: 16px;
      top: 16px;
    }

    @for $i from 1 through 6 {
      &:nth-child(#{$i}) {
        animation-delay: $i * (3s/2);
      }
    }
  }

  .checkmark {
    position: relative;
    padding: 30px;
    animation: checkmark 5m cubic-bezier(0.42, 0, 0.275, 1.155) both;
    display: inline-block;
    transform: scale(0.8);
    margin: -20px;

    &__check {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 10;
      transform: translate3d(-50%, -50%, 0);
      fill: white;
    }

    &__background {
      fill: var(--theme-color);
      animation: rotate 35s linear both infinite;
    }
  }

  i {
    font-size: 50px;
    color: #4ead4e;
  }

  h2 {
    margin-top: 10px;
    margin-bottom: 15px;
  }

  p {
    font-size: 18px;
    text-transform: capitalize;
  }

  &.order-fail {
    i {
      color: var(--theme-color);
    }
  }
}

