        margin-bottom: 8px;
        font-size: 18px;
        color: $dark-font;
        text-transform: capitalize;
      }

      &.product-pagination {
        border: none;
        margin: 0;

        .pagination {
          .page-item {
            border-top: 1px solid $round-border;
            border-bottom: 1px solid $round-border;

            a {
              padding: 10px 14px;
            }
          }
        }
