      p {
        line-height: 1.5;
        margin-bottom: 0;
      }
    }
  }
}

.start-selling {
  h4 {
    margin-bottom: 10px;
    text-transform: capitalize;
    font-weight: 600;
    line-height: 1.3;
  }

  p {
    line-height: 2;
    letter-spacing: 0.03em;
  }

  .btn {
    margin-top: 20px;
  }
}

// vendor dashboard
.vendor-dashboard {
  .dashboard-box {
    padding: 20px 10px;
    background-color: $grey-lighter;

    .date-picker-box {
      display: flex;

      .input-group {
        width: 50%;
        padding: 0 10px;
      }
    }
  }
}

// dashboard
.dashboard-section {
  .dashboard-sidebar {
    position: sticky;
    top: 30px;
    z-index: 1;
    padding: 30px 0 15px 0;
    background-color: $top-header;

    .profile-top {
      padding: 0 16px;
      margin-bottom: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));

      .profile-image {
        position: relative;

        img {
          width: 130px;
          margin: 0 auto;
          display: block;
          border-radius: 100%;
        }

        .profile-edit {
          position: absolute;
          bottom: 0;
          right: 30%;

          svg {
            stroke: var(--theme-color);
          }
        }
      }

      .profile-detail {
        text-align: center;
        margin-top: 15px;

        h5 {
          text-transform: capitalize;
          font-weight: 700;
          margin-bottom: 5px;
        }

        h6 {
          color: $grey;
          margin-bottom: 3px;
        }
      }
    }

    .faq-tab {
      .nav-tabs {
        box-shadow: none;
        padding: 0;
      }
    }
  }

  .counter-section {
    .counter-box {
      background-color: $top-header;
      padding: 30px 30px;
      display: flex;
      align-items: center;
      border-left: 3px solid var(--theme-color);
      border-radius: 8px;

      img {
        height: 50px;
        margin-right: 20px;
      }

      h3 {
        font-weight: 700;
        color: var(--theme-color);
        margin-bottom: 4px;
        font-size: 20px;
      }

      h5 {
        margin-bottom: 0;
        text-transform: capitalize;
      }
    }
  }

  .faq-content {
    .card {
      border: none;
      background-color: $top-header;
      margin-top: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
    }
  }

  .dashboard-table {
    h3 {
      text-transform: capitalize;
      font-size: 17px;
      color: $dark-font;
      margin-bottom: 15px;
      font-weight: 600;
    }

    img {
      width: 50px;
    }

    .table {
      thead {
        th {
          border-top: none;
        }
      }

      th,
      td {
        vertical-align: middle;
        text-transform: capitalize;
        text-align: center;
      }
    }
  }

  .top-sec {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    h3 {
      text-transform: capitalize;
      color: $black;
      font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
      margin-bottom: 0;
      font-weight: 600;
    }

    .btn {
      margin-left: auto;
      text-transform: capitalize;
      padding: calc(5px + (7 - 5) * ((100vw - 320px) / (1920 - 320)))
        calc(7px + (14 - 7) * ((100vw - 320px) / (1920 - 320)));
    }
  }

  .dashboard-box {
    .dashboard-title {
      margin-bottom: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
      display: flex;
      align-items: center;

      h4 {
        text-transform: capitalize;
        font-weight: 700;
        margin-bottom: 0;
      }

      span {
        margin-left: 30px;
        padding: 2px 16px;
        border-radius: 2px;
        text-transform: capitalize;
        color: var(--theme-color);
        cursor: pointer;
      }
    }

    .dashboard-detail {
      ul {
        li {
          display: block;
          margin-bottom: 10px;

          .details {
            display: flex;

            h6 {
              text-transform: capitalize;
              margin-bottom: 0;
            }

            span {
              margin-left: calc(
                10px + (30 - 10) * ((100vw - 320px) / (1920 - 320))
              );
              border-radius: 2px;
              text-transform: capitalize;
              color: var(--theme-color);
              cursor: pointer;
            }

            .left {
              width: 150px;
              margin-right: 15px;

              h6 {
                color: #4e4e4e;
              }
            }

            .right {
              display: flex;
              align-items: center;

              h6 {
                font-weight: 500;
              }
            }
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      .account-setting {
        h5 {
          margin-bottom: 15px;
          text-transform: capitalize;
        }

        .row {
          > div {
            .form-check {
              margin-bottom: 5px;
              padding-left: 0;
              color: #606060;

              &:last-child {
                margin-bottom: 0;
              }
            }
          }
        }

        .btn {
          margin-top: 0;
        }

        + .account-setting {
          margin-top: 20px;
        }
      }
    }
  }

  .radio_animated {
    position: relative;
    margin: 4px 1rem 0 0;
    cursor: pointer;

    &:before {
      transition: transform 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
      transform: scale(0, 0);
      content: "";
      position: absolute;
      top: 0;
      left: 0.125rem;
      z-index: 1;
      width: 0.75rem;
      height: 0.75rem;
      background: var(--theme-color);
      border-radius: 50%;
    }

    &:after {
      content: "";
      position: absolute;
      top: -0.25rem;
      left: -0.125rem;
      width: 1.25rem;
      height: 1.25rem;
      background: $white;
      border: 2px solid $light-gray;
      border-radius: 50%;
    }

    &:checked:before {
      transform: scale(1, 1);
    }
  }

  .apexcharts-toolbar {
    z-index: 1;
  }
}

.faq-tab {
  .nav-tabs {
    display: block;
    border-bottom: none;
    background-color: $white;
    padding: 10px 0;

    .nav-item {
      display: block;
      margin-bottom: 0;

      .nav-link {
        text-transform: capitalize;
        color: $black;
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
        border: none;
        transition: all 0.5s ease;
        border-radius: 0;
        background-color: $top-header;
        cursor: pointer;

        &.active {
          border: none;
          border-right: 2px solid var(--theme-color);
          border-radius: 0;
          color: var(--theme-color);
          transition: all 0.5s ease;
        }

        &:hover {
          border: none;
          border-right: 2px solid var(--theme-color);
          color: var(--theme-color);
          transition: all 0.5s ease;
        }
      }
    }
  }
}

// order tracking
.tracking-page {
  h3 {
    margin-bottom: 20px;
    text-transform: uppercase;
    color: $dark-font;
    font-weight: 700;
    margin-top: -5px;
    font-size: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
  }

  .border-part {
    border-bottom: 1px dashed $round-border;
    padding-bottom: 25px;
    padding-top: 25px;
    border-top: 1px dashed $round-border;
  }

  .tracking-detail {
    display: flex;
    align-items: center;
    height: 100%;

    ul {
      li {
        display: flex;
        font-size: 16px;
        margin-bottom: 5px;

        .left {
          min-width: 220px;
          text-transform: capitalize;
          font-weight: 600;
        }

        .right {
          color: $grey;
        }
      }
    }
  }

  .order-map {
    height: 100%;

    iframe {
      height: 100%;
      width: 100%;
      border: 5px solid $white;
      box-shadow: 0px 5px 23px rgba($black, 0.09);
      border-radius: 8px;
    }
  }

  .wrapper {
    margin: 30px 0;
    margin-left: -5px;

    .arrow-steps {
      .step {
        font-size: 14px;
        text-align: center;
        color: $black;
        cursor: default;
        margin: 0 5px;
        padding: 12px 10px 12px 30px;
        min-width: 180px;
        float: left;
        position: relative;
        background-color: $top-header;
        user-select: none;
        transition: background-color 0.2s ease;

        span {
          position: relative;
          font-size: 16px;
          font-weight: 500;
          text-transform: capitalize;

          &:before {
            opacity: 0;
            content: "✔";
            position: absolute;
            top: -2px;
            left: -20px;
          }
        }

        &:before,
        &:after {
          content: " ";
          position: absolute;
          top: 0;
          right: -17px;
          width: 0;
          height: 0;
          border-top: 25px solid transparent;
          border-bottom: 22px solid transparent;
          border-left: 17px solid $top-header;
          z-index: 2;
          transition: border-color 0.2s ease;
        }

        &:before {
          right: auto;
          left: 0;
          border-left: 17px solid $white;
          z-index: 0;
        }

        &:first-child {
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;

          &:before {
            border: none;
          }
        }

        &.done {
          span {
            &:before {
              opacity: 1;
              transition: opacity 0.3s ease 0.5s;
            }
          }
        }

        &.current {
          color: $white;
          background-color: var(--theme-color);

          &:after {
            border-left: 17px solid var(--theme-color);
          }
        }
      }
    }
  }

  .table {
    th {
      padding-top: 0;
    }
  }

  .table-striped {
    tbody {
      tr {
        &:nth-of-type(odd) {
          background-color: $top-header;
        }
      }
    }
  }

  .table th,
  .table td {
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    text-transform: capitalize;
  }
}
