/*=====================
    Demo CSS 
==========================*/

// Full-page scroll CSS start
.full-scroll-menu {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: transparent;

  .container-fluid {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.full-scroll-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;

  .sub-footer {
    background-color: transparent;

    .container-fluid {
      padding-left: 60px;
      padding-right: 60px;
    }
  }
}

.home-slider {
  &.fullpage {
    .home {
      height: calc(100vh - 2px);

      .slider-contain {
        height: calc(100vh - 2px);
        padding-left: 25px;

        h1 {
          font-style: italic;
          font-size: 50px;
          margin-top: -12px;
        }

        p {
          max-width: 600px;
          line-height: 1.5;
          margin-top: 15px;
          margin-bottom: 10px;
          font-size: 16px;
          color: #777777;
        }
      }
    }

    .slick-next,
    .slick-prev {
      display: none !important;
    }

    &:hover {
      .slick-next,
      .slick-prev {
        display: none !important;
      }
    }

    .slick-dots {
      top: 50%;
      bottom: unset;
      width: unset;
      left: 20px;
      transform: translateY(-50%);

      li {
        display: flex;

        button {
          &:before {
            font-size: 15px;
            color: var(--theme-color);
            opacity: 0.2;
          }
        }
      }

      li {
        &.slick-active {
          button {
            &:before {
              opacity: 0.8;
            }
          }
        }
      }
    }
  }

  &.instagram-slider {
    .home {
      height: 50vh;

      .slider-contain {
        height: 50vh;
      }
    }
  }

  &.slick-dotted {
    &.slick-slider {
      margin-bottom: 0;
    }
  }
}

.video-layout {
  height: calc(100vh - 5px);
  width: 100vw;
  object-fit: fill;
}

// Electronics Demo CSS
.layout-8 {
  padding: 0 80px;

  .layout-8-bg {
    background-image: url("../images/electronics/bg.jpg");
    background-size: cover;
    background-attachment: fixed;
    height: 100%;
    width: 100%;
    padding: 0 15px;
    animation: 10s linear 0s normal none infinite animate;

    .layout7-product {
      .product-box {
        .details-product {
          opacity: 1;
        }

        &:hover {
          .details-product {
            animation: none;
          }
        }
      }
    }
  }
}

.white-bg {
  background-color: $white !important;
}

// Fashion-3 Demo CSS
.box-layout-body {
  background-color: $grey-lighter;

  .box-layout-header {
    background-color: $white;
  }

  .box-layout {
    margin-top: -70px;
    margin-bottom: 5px;
    box-shadow: 0 0 8px 0 #ddd;
    position: relative;
    background-color: white;

    .full-banner {
      margin-left: -15px;
      margin-right: -15px;
      padding-top: calc(100px + (200 - 100) * ((100vw - 320px) / (1920 - 320)));
      padding-bottom: calc(
        100px + (200 - 100) * ((100vw - 320px) / (1920 - 320))
      );
    }

    &.bg-image {
      background-image: url("../images/bg-image.jpg");
    }
  }
}

// Beauty Demo css
.beauty-about {
  .about-text {
    p {
      text-align: left;
    }
  }

  &.border-about {
    .border-cls {
      position: relative;

      &:after {
        position: absolute;
        content: "";
        width: 90%;
        height: 95%;
        left: -10px;
        top: -20px;
        border: 10px solid var(--theme-color);
        z-index: -1;
      }
    }

    .service-block1 {
      svg {
        margin-bottom: 6px;
      }

      &:after {
        content: "";
        position: absolute;
        width: 40px;
        height: 40px;
        right: 30%;
        top: 0;
        background-color: var(--theme-color);
        z-index: -1;
        opacity: 0.18;
      }
    }

    .about-section {
      .small-section {
        padding-top: 0;
      }
    }
  }
}

.about-section {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  .small-section {
    padding-top: 20px;
  }

  .service {
    .service-block1 {
      img {
        margin-bottom: 10px;
      }

      h5 {
        text-transform: capitalize;
        font-weight: 700;
        margin-bottom: 0;
      }
    }
  }
}

.video-modal {
  .modal-body {
    padding: 0;
  }

  iframe {
    height: 500px;
    width: 100%;
    vertical-align: middle;
    border: none;
  }
}

.video-section {
  .video-img {
    position: relative;
    overflow: hidden;

    img {
      transition: all 0.5s ease;
      transform: scale(1);
    }

    .play-btn {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      span {
        height: 60px;
        width: 60px;
        border-radius: 100%;
        background-color: var(--theme-color);
        text-align: center;
        justify-content: center;
        align-items: center;
        display: flex;
      }

      i {
        font-size: 35px;
        padding-left: 5px;
        color: $white;
      }
    }

    &:hover {
      img {
        transform: scale(1.03);
        transition: all 0.5s ease;
      }
    }
  }
}

