// Game Demo css
.game-back {
  background-image: url("https://i.pinimg.com/originals/e0/80/08/e08008c19c8e9e1b0074baf36d699407.jpg");
  background-blend-mode: overlay;
  background-color: rgba($black, 0.6);

  .pixelstrap {
    &.sm-horizontal {
      > li {
        > a {
          color: white;

          &:hover,
          &.active {
            color: white;
          }
        }
      }
    }
  }

  .onhover-div {
    img {
      filter: brightness(100);
    }
  }

  .main-menu {
    &.border-section {
      border-bottom: none;
    }

    .menu-left {
      .navbar {
        i {
          color: $white;
        }
      }
    }
  }
}

.main-banner-bg {
  background: url("../images/main-banner-bg.jpg");
  padding: 45px 0;

  .small-slider {
    border: 20px solid $white;

    .home-slider {
      .home {
        .slider-contain {
          align-items: flex-end;

          h1,
          h4 {
            color: $white;
          }

          > div {
            margin-bottom: 30px;
          }
        }
      }
    }
  }
}

.game-banner {
  .banner-timer {
    background-image: url(../images/banner-game.jpg);
    background-blend-mode: overlay;
    background-color: rgba($black, 0.4);
    margin-left: 0;
    margin-right: 0;

    .banner-text {
      h2 {
        color: $white;
      }
    }
  }

  .timer {
    background-color: rgba(#2d2a25, 0.4);
  }
}

.game-slider {
  .theme-card {
    .offer-slider {
      img {
        height: 220px;
      }
    }
  }
}

.bg-footer {
  background: url("../images/game/footer.jpg");
  background-position: bottom;

  .light-layout {
    background-color: transparent;
  }
}

.game-parallax {
  .full-banner {
    &.parallax {
      background-blend-mode: overlay;
      background-color: rgba($black, 0.6);
    }

    .banner-contain {
      h3,
      h4 {
        color: $white;
      }
    }
  }
}

.effect-cls {
  position: relative;

  &:after {
    content: "";
    position: absolute;
    background: url(../images/game/top.png);
    top: -1px;
    width: 100%;
    height: 85px;
    z-index: 1;
    background-repeat: no-repeat;
  }

  &:before {
    content: "";
    position: absolute;
    background: url("../images/game/back.png");
    bottom: -12px;
    width: 100%;
    height: 110px;
    z-index: 1;
  }

  &.footer-effect {
    &:before {
      display: none;
    }
  }
}

//  Gym demo CSS start
.gym-parallax {
  .title1 {
    h2 {
      color: white;
    }
  }

  .product-para {
    p {
      color: white;
    }
  }

  .instagram-box {
    .overlay {
      background: linear-gradient(
        -180deg,
        var(--theme-color) 0%,
        var(--theme-color2) 99%
      );
    }
  }
}

.gym-product {
  text-align: center;

  .product-box {
    background-color: white;
    padding: 15px;
    box-shadow: 0 0 8px 0 #ddd;
    transition: all 0.5s ease;

    .img-wrapper {
      position: unset;

      .cart-info {
        width: 100%;
        z-index: 1;
        display: flex;
        left: 0;
        height: 100%;
        align-items: center;
        bottom: 0;
        top: 0;

        i {
          background: linear-gradient(
            180deg,
            var(--theme-color) 0%,
            var(--theme-color2) 99%
          );
          color: rgba($white, 0.6);
          border-radius: 100%;
          margin: 0 5px;
          transition: all 0.5s ease;
        }

        button,
        a {
          &:hover {
            i {
              color: white;
            }
          }
        }
      }
    }

    .product-detail {
      h6 {
        color: $grey;
      }

      h4 {
        color: $dark-font !important;
      }
    }

    &:hover {
      transform: scale(1.02);
      transition: all 0.5s ease;

      .img-wrapper {
        .cart-info {
          opacity: 1;
          transition: all 0.5s ease;

          button {
            animation: fadeInUp 300ms ease-in-out;
          }

          a {
            &:nth-child(2) i {
              animation: fadeInUp 500ms ease-in-out;
            }

            &:nth-child(3) i {
              animation: fadeInUp 700ms ease-in-out;
            }

            &:nth-child(4) i {
              animation: fadeInUp 1000ms ease-in-out;
            }
          }
        }
      }
    }
  }

  .partition-cls {
    > div {
      &:nth-child(-n + 4) {
        margin-bottom: 30px;
      }
    }
  }
}

.gym-blog {
  .blog-details {
    text-align: left;
    padding-left: 0;

    p {
      margin-bottom: 3px;
    }
  }
}

.gym-banner {
  .collection-banner {
    .contain-banner {
      h4,
      h2 {
        color: $white;
      }
    }
  }
}

.bg-img-gym {
  //background-image: url("../images/gym/bg.jpg");
  &.overlay-cls {
    background-blend-mode: overlay;
    background-color: rgba($black, 0.85);
  }

  .dark-layout {
    background-color: transparent;
  }

  .footer-theme2 {
    p {
      color: $white;
    }

    .contact-details {
      li {
        color: $white;

        a {
          color: $white;
        }
      }
    }

    .footer-link,
    .footer-link-b {
      a {
        color: #ebebeb;
      }
    }
  }

  .sub-footer {
    &.darker-subfooter {
      background-color: black;

      p {
        color: $white;
      }
    }
  }
}

//  Jewellery demo CSS start
.jewel-footer {
  .sub-footer {
    &.black-subfooter {
      background-color: $border-grey;
    }
  }
}

.home {
  &.bg-position {
    background-position: top;
  }
}

//  pets demo CSS start
.pets-box {
  .product-box {
    .img-wrapper {
      .cart-info {
        position: absolute;
        padding: 10px;
        border: none;
        transform: perspective(500px) rotateX(90deg);
        bottom: 0;
        z-index: 1;

        &:after {
          position: absolute;
          content: "";
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          background-color: var(--theme-color);
          opacity: 0.3;
          z-index: -1;
        }

        i {
          padding: 5px;
          margin: 0 3px;
          background-color: white;
        }

        a {
          border-left: none;
        }
      }
    }

    .product-detail {
      text-align: center;
      background-color: #fafafa;

      .rating {
        margin-top: 0;
        padding-top: 15px;
      }
    }

    &:hover {
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.08);

      .cart-info {
        transform: perspective(500px) rotateX(0deg);
      }
    }
  }
}

footer {
  &.pet-layout-footer {
    .white-layout {
      background-color: #f5e1d0;
    }

    .footer-brand-logo {
      margin-bottom: 20px;
    }

    .sub-footer {
      &.black-subfooter {
        background-color: var(--theme-color);
      }

      p {
        padding: 15px 0;
        color: $white;
      }
    }

    .social-white {
      display: block;
      margin-top: 40px;
      margin-bottom: 30px;
    }
  }
}

.form_search {
  height: 45px;
  box-shadow: 0 0 0 1px #e2e2e2;
  overflow: hidden;
  position: relative;
  width: 700px;
  background-color: #f5f5f5;
  border-radius: 25px;

  input {
    width: 100%;
    height: 45px;
    font-size: 16px;
    color: darkgrey;
    border: none;
    outline: none;
    background: none;
    padding: 0 10px;
  }

  button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 40px;
    height: 35px;
    padding: 0;
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
      font-size: 18px;
      color: var(--theme-color);
    }
  }
}

.blog {
  &.blog_box {
    .blog-details {
      text-align: left;
      padding: 0;

      p {
        font-size: 14px;
        text-transform: none;
        color: $font-color;
        font-weight: 400;
        margin-bottom: 8px;
        margin-top: 3px;
      }

      a {
        p {
          color: $grey7;
          text-transform: capitalize;
          font-weight: 700;
          margin-bottom: 0;
          margin-top: 10px;
        }
      }

      .read-cls {
        text-transform: capitalize;
        font-weight: 700;
        color: $black;
      }
    }
  }
}

// tools demo CSS start
.tools-bg {
  background-color: #f5f5f5;

  section {
