// nft home page

.nft-home {
  .home-slider {
    .home {
      background-color: #151515;
    }
    .slider-img {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      padding-top: 70px;
      img {
        width: 50%;
        animation: float 5s ease-in-out infinite;
        &.sm-img {
          width: 41%;
          margin-right: -70px;
          animation: float 6s ease-in-out infinite;
          @media (max-width: 991px) {
            width: 50%;
            margin-right: -50px;
          }
          @media (max-width: 767px) {
            width: 66%;
          }
        }
        @media (max-width: 991px) {
          width: 60%;
        }
        @media (max-width: 991px) {
          width: 70%;
        }
      }
    }
    .slider-contain {
      padding-top: 70px;
      [dir="rtl"] & {
        text-align: right;
      }
      @media (max-width: 576px) {
        padding-top: 55px;
      }
      p {
        line-height: 30px;
        font-size: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
        color: #777777;
      }

      h1 {
        text-transform: capitalize;
        font-size: calc(20px + (56 - 20) * ((100vw - 320px) / (1920 - 320)));
        margin-top: 0;
        margin-bottom: 6px;
        color: $white;
      }

      h3 {
        margin-top: 14px;
        margin-bottom: 0;
        font-weight: 600;
        color: #777777;

        del {
          margin-left: 10px;
        }
      }
    }
  }
}

.nft-box {
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 15px;
  [dir="rtl"] & {
    direction: rtl;
  }
  .nft-img {
    position: relative;
    border-radius: 10px;
    overflow: hidden;
    .timer-sec {
      position: absolute;
      right: 40px;
      top: 8px;
      background-color: rgba(255, 255, 255, 0.7);
      border-radius: 26px;
      padding: 2px 10px;
      [dir="rtl"] & {
        left: 40px;
        right: unset;
      }
    }
    a {
      position: absolute;
      right: 10px;
      top: 7px;
      width: 25px;
      color: #5c5c5c;
      height: 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 100%;
      background-color: rgba(255, 255, 255, 0.7);
      border-radius: 100%;
      padding: 5px;
      backdrop-filter: blur(10px);
      [dir="rtl"] & {
        right: unset;
        left: 10px;
      }
    }
  }
  .nft-content {
    .top-content {
      margin-top: 14px;
      h4 {
        font-weight: 600;
        color: #222222;
        margin-bottom: 4px;
      }
      h5 {
        color: #777777;
      }
    }
    .bottom-content {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      span {
        color: #777777;
        display: block;
        margin-bottom: 3px;
      }
      .price {
        display: flex;
        align-items: center;
        gap: 6px;
        img {
          width: 18px;
        }
        h5 {
          margin-bottom: 0;
        }
      }
    }
  }
}

.nft-feature-sec {
  .step-bg {
    h4 {
      margin-bottom: 10px;
      color: $white;
      font-size: 25px;
    }

    .step-box {
      position: relative;
      padding: 50px 35px 33px;
      box-shadow: 0 15px 39px 0 rgba(8, 18, 109, 0.08);
      transition: all 0.3s ease;
      background-color: #262935;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      border-radius: 25px;

      .steps {
        position: relative;
        width: 80px;
        height: 80px;
        background: #1b1b1d;
        margin: 0 auto 25px auto;
        color: $white;
        text-align: center;
        font-weight: 500;
        border-radius: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: calc(28px + (40 - 28) * ((100vw - 320px) / (1920 - 320)));
        padding: 8px;
      }

      p {
        line-height: 1.5;
        margin-bottom: 0;
        color: rgba($white, 0.72);
        font-size: 16px;
      }
    }
  }
}

.nft-top-collection {
  .collection-panal {
    .nft-collection {
      display: flex;
      align-items: center;
      gap: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320)));
      [dir="rtl"] & {
        direction: rtl;
      }
      + .nft-collection {
        padding-top: 15px;
        margin-top: 15px;
        border-top: 1px solid #eee;
      }
      .number {
        font-size: calc(13px + (22 - 13) * ((100vw - 320px) / (1920 - 320)));
        width: calc(15px + (25 - 15) * ((100vw - 320px) / (1920 - 320)));
        color: #5a5a5a;
      }
      .img-part {
        img {
          width: calc(60px + (70 - 60) * ((100vw - 320px) / (1920 - 320)));
          height: calc(60px + (70 - 60) * ((100vw - 320px) / (1920 - 320)));
          border-radius: 7px;
          overflow: hidden;
          object-fit: cover;
        }
      }
      .content {
        width: calc(100% - 120px);
        @media (max-width: 576px) {
          width: calc(100% - 100px);
        }
        h5 {
          font-weight: 600;
          font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
          margin-bottom: calc(
            5px + (10 - 5) * ((100vw - 320px) / (1920 - 320))
          );
          display: flex;
          align-items: center;
          gap: 5px;
          img {
            width: calc(16px + (19 - 16) * ((100vw - 320px) / (1920 - 320)));
            height: auto;
            margin-top: -3px;
          }
        }
        h6 {
          margin-bottom: 0;
          font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
          display: flex;
          align-items: center;
          justify-content: space-between;
          line-height: calc(
            17px + (24 - 17) * ((100vw - 320px) / (1920 - 320))
          );
          &:nth-child(2) {
            margin-top: -4px;
          }
        }
      }
    }
  }
}

