      font-weight: 700;
      text-transform: uppercase;
      transform: translate(50%, -50%) rotate(-90deg);
      text-align: center;
      background-color: var(--theme-color);
      margin-bottom: 0;
      cursor: pointer;
    }
  }
}

.cart-error-toast {
  .toast {
    width: calc(100% - 50px);
    left: 0 !important;
    background-color: #fc8f8f;
    color: black;
    font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
    padding: calc(15px + (22 - 15) * ((100vw - 320px) / (1920 - 320)))
      calc(20px + (42 - 20) * ((100vw - 320px) / (1920 - 320)));
    margin: 0;
    left: 50% !important;
    transform: translateX(-50%);
    @media (max-width: 576px) {
      left: 0 !important;
      width: 100vw;
      transform: none;
    }
    .error-icon {
      margin-right: calc(14px + (32 - 14) * ((100vw - 320px) / (1920 - 320)));
      width: calc(22px + (33 - 22) * ((100vw - 320px) / (1920 - 320)));
      height: calc(22px + (33 - 22) * ((100vw - 320px) / (1920 - 320)));
    }
    .close-icon {
      width: 22px;
    }
  }
  .toast-content {
    @media (max-width: 576px) {
      margin-top: -4px;
    }
  }
}

