/**=====================
    Header css start
==========================**/

header {
  background-color: $white;

  &.header-absolute {
    position: absolute;
    width: 100%;
    top: 62px;

    .main-menu {
      background-color: $white;

      .menu-left {
        .navbar {
          padding: 20px 45px 20px 0;
        }
      }

      .brand-logo {
        padding-top: 20px;
        padding-bottom: 20px;

        img {
          width: 150px;
        }
      }
    }

    .pixelstrap {
      > li {
        > a {
          padding-top: 20px;
          padding-bottom: 20px;

          &:hover,
          &:active,
          &:focus,
          &.highlighted {
            padding-top: 20px;
            padding-bottom: 20px;
          }
        }
      }

      li {
        .lable-nav {
          top: 0;
        }
      }
    }

    .onhover-div {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &.header-gym {
    position: absolute;
    width: 100%;

    .main-menu {
      background-color: rgba($black, 0.8);
      padding: 0 15px;
      z-index: 1;

      .menu-left {
        .navbar {
          padding: 20px 45px 20px 0;

          i {
            color: $white;
          }
        }
      }

      .brand-logo {
        padding-top: 20px;
        padding-bottom: 20px;
      }

      .onhover-div {
        .show-div {
          top: 70px;
        }
      }
    }

    .pixelstrap {
      > li {
        > a {
          padding-top: 20px;
          padding-bottom: 20px;
          color: $white;

          &:hover,
          &:active,
          &:focus,
          &.highlighted {
            padding-top: 20px;
            padding-bottom: 20px;
            color: $white;
          }
        }
      }
    }

    .onhover-div {
      padding-top: 20px;
      padding-bottom: 20px;

      > div {
        img {
          filter: brightness(54);
        }
      }
    }

    .top-header {
      z-index: 9;
      position: relative;
      background: transparent;

      .header-contact {
        li {
          color: $white;

          i {
            color: $white;
          }
        }
      }

      .header-dropdown {
        li {
          color: $white;

          a {
            color: $white;

            i {
              color: $white;
              padding-right: 8px;
            }
          }
        }
      }

      &.blue-layout {
        border-bottom: 1px solid #223b7d;
      }
    }

    &.sticky {
      width: 100%;
      background-color: $black;
    }
  }

  &.header-black {
    background-color: $black;

    .main-menu {
      .menu-left {
        .navbar {
          padding: 20px 45px 20px 0;

          i {
            color: $white;
          }
        }
      }

      .brand-logo {
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }

    .pixelstrap {
      > li {
        > a {
          padding-top: 20px;
          padding-bottom: 20px;
          color: $gray-90;

          &:hover,
          &:active,
          &:focus,
          &.highlighted {
            padding-top: 20px;
            padding-bottom: 20px;
            color: $gray-90;
          }
        }
      }

      li {
        .lable-nav {
          top: 0;
        }
      }
    }

    .onhover-div {
      padding-top: 20px;
      padding-bottom: 20px;

      > div {
        img {
          filter: brightness(54);
        }
      }
    }
  }

  &.header-5 {
    position: absolute;
    width: 100%;
    background-color: transparent;

    .top-header {
      background-color: transparent;

      .header-dropdown {
        > li {
          padding-top: 40px;
          padding-right: 0;

          &.mobile-account {
            &:before {
              display: none;
            }
          }
        }

        .onhover-dropdown {
          .onhover-show-div {
            top: 80px;
          }
        }
      }
    }

    .onhover-div {
      .show-div {
        top: 80px;
      }
    }

    &.container-med {
      .container-fluid {
        padding-left: 50px;
        padding-right: 50px;
      }
    }

    &.sticky {
      width: 100%;
      background-color: $white;
    }

    &.none-sticky {
      &.sticky {
        position: unset;
      }

      &.stickycls {
        position: unset;
      }
    }

    &.left-sidebar-header {
      position: fixed;
      background-color: $white;
      z-index: 1;

      .container-fluid {
        padding-left: 40px;
        padding-right: 40px;
      }

      .onhover-div {
        padding-top: 20px;
        padding-bottom: 20px;
      }

      .top-header {
        .header-dropdown {
          > li {
            padding-top: 20px;
          }
        }
      }

      .main-menu {
        .brand-logo {
          padding-top: 20px;
          padding-bottom: 20px;
        }
      }
    }

    &.overlay-style {
      background-color: rgba($black, 0.28);
      z-index: 1;

      .pixelstrap {
        > li {
          > a {
            color: $white;

            &:hover,
            &:active {
              color: $white;
            }
          }
        }
      }
    }
  }

  &.header-6 {
    .top-header {
      background-color: var(--theme-color);

      .header-contact {
        li {
          color: $white;

          i {
            color: $white;
          }
        }
      }

      .header-dropdown {
        li {
          color: $white;

          a {
            color: $white;

            i {
              color: $white;
            }
          }
        }
      }
    }

    .mobile-search {
      display: none;
    }
  }

  &.header-7 {
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
    background-color: transparent;

    .main-menu {
      .menu-left {
        .navbar {
          padding: 25px 45px 25px 0;

          i {
            color: $white;
          }
        }
      }

      .brand-logo {
        padding-top: 25px;
        padding-bottom: 25px;
      }
    }

    .onhover-div {
      padding-top: 25px;
      padding-bottom: 25px;

      img {
        filter: brightness(100);
      }

      .show-div {
        img {
          filter: unset;
        }
      }

      &:hover {
        .show-div {
          transform: translateY(-23px);
        }
      }
    }

    .game-layout {
      background-color: rgba($black, 0.5);
    }

    .pixelstrap {
      &.sm-horizontal {
        > li {
          > a {
            color: $white;

            &:hover,
            &:active {
              color: $white;
            }
          }
        }
      }
    }

    .top-header {
      background-color: transparent;

      &.top-header-dark {
        .header-contact {
          li {
            color: $white;

            i {
              color: $white;
            }
          }
        }

        .header-dropdown {
          > li {
            color: $white;

            > a {
              color: $white;

              i {
                color: $white;
              }
            }
          }
        }
      }
    }
  }

  &.header-tools {
    position: absolute;
    width: 100%;
    top: 35px;
    z-index: 1;
    background-color: transparent;

    .container {
      padding-left: 30px;
      padding-right: 30px;

      &.rounded-5 {
        border-radius: 5px;
      }
    }

    .top-header {
      background-color: transparent;

      .container {
        background-color: var(--theme-color);
      }

      .header-contact {
        padding: 10px 0;

        li {
          color: $white;
        }
      }

      .header-dropdown {
        li {
          color: $white;
          padding: 10px 25px;

          a {
            i {
              color: $white;
            }
          }
        }
      }
    }

    .logo-menu-part {
      > .container {
        background-color: $white;
        border-bottom: 2px solid var(--theme-color);
      }
    }

    .main-menu {
      .menu-left {
        .navbar {
          padding: 25px 45px 25px 0;
        }
      }

      .brand-logo {
        padding-top: 25px;
        padding-bottom: 25px;
      }
    }

    .pixelstrap {
      &.sm-horizontal {
        > li {
          > a {
            padding-top: 25px;
            padding-bottom: 25px;

            &:hover,
            &:active,
            &:focus,
            &.highlighted {
              padding-top: 25px;
              padding-bottom: 25px;
            }
          }
        }

        li {
          .lable-nav {
            top: 5px;
          }
        }
      }
    }

    .onhover-div {
      padding-top: 25px;
      padding-bottom: 25px;
    }

    &.sticky {
      background-color: $white;
      transition: none;

      .logo-menu-part {
        > .container {
          border-bottom: 0;
        }
      }
    }

    &.header-style {
      &.top-relative {
        position: relative;
        width: 100%;
        top: 0;
        background-color: $white;
        box-shadow: 0 0 8px $round-border;

        .container {
          padding-left: 15px;
          padding-right: 15px;
        }
      }
    }
  }

  &.green-gradient {
    .top-header {
      background: var(--theme-color);
      background-image: linear-gradient(
        to right,
        var(--theme-color),
        var(--theme-color2)
      );

      .header-contact {
        padding: 10px 0;

        li {
          color: $white;

          i {
            color: $white;
          }
        }
      }

      .header-dropdown {
        li {
          color: $white;
          padding: 10px 25px;

          a {
            color: $white;

            i {
              color: $white;
            }
          }
        }
      }
    }

    .pixelstrap {
      li {
        .lable-nav {
          background: var(--theme-color);
          background-image: linear-gradient(
            to right,
            var(--theme-color),
            var(--theme-color2)
          );
        }
      }
    }
  }

  &.header-metro {
    position: absolute;
    width: 100%;
    background-color: transparent;

    .top-header {
      background-color: transparent;
      z-index: 1;
      position: relative;
    }

    .metro {
      background-color: $white;
      z-index: 1;
      position: relative;
    }
  }

  &.left-header {
    .top-header {
      padding-left: 300px;
      background-color: $white;

      .header-dropdown {
        .mobile-wishlist,
        .mobile-account {
          img {
            display: block;
          }

          i {
            display: none;
          }
        }
      }
    }

    .onhover-div {
      padding-top: 15px;
      padding-bottom: 15px;

      > div {
        img {
          width: 20px;
          height: auto;
        }
      }

      .show-div {
        top: 56px;
      }
    }

    .sidenav {
      left: 0;
      box-shadow: 0 0 1px 0 rgba($black, 0.25);

      .sidebar-back {
        display: none;
      }

      nav {
        background-color: $white;
      }

      .pixelstrap {
        > li {
          > a {
            padding: 10px 0 20px 0;
            background-color: $white;

            &:hover,
            &:focus,
            &.highlighted {
              padding: 10px 0 20px 0;
              background-color: $white;
            }
          }

          .lable-nav {
            top: -12px;
            background-color: $white-dark;

            &:before {
              border-top: 5px solid $color-red;
            }

            &.grey-lable {
              background-color: #4d6171;

              &:before {
                border-top: 5px solid #4d6171;
              }
            }
          }
        }
      }

      .left-sidebar_search {
        width: 100%;
        background-color: $white;

        input {
          height: 55px;
          width: 95%;
          border: none;
          padding-left: 15px;
          background-color: transparent;
        }

        .btn-search {
          position: absolute;
          right: 10px;
          top: 20px;
          border: none;
          background-color: transparent;
        }
      }

      .left-sidebar_center {
        padding: 40px 0 30px 30px;
        background-color: $white;
      }

      .bottom-section {
        padding: 70px 25px;

        .icon_settings {
          li {
            display: inline-block;
          }

          .shopping-cart {
            position: relative;

            .shopping-count {
              width: 18px;
              height: 18px;
              background-color: $color-red;
              color: $white;
              position: absolute;
              border-radius: 100%;
              display: flex;
              align-items: center;
              justify-content: center;
              top: -7px;
              right: 7px;
            }
          }
        }

        ul {
          li {
            display: inline-block;
            padding: 8px 15px 8px 0;

            a {
              font-size: 16px;
              text-transform: capitalize;
              color: $dark-font;

              i {
                font-size: 18px;
                padding-right: 5px;
                color: $dark-font;
              }
            }
          }
        }

        .leftside_setting {
          .language,
          .currency {
            display: flex;

            h5 {
              margin-bottom: 0;
              text-transform: capitalize;
              color: #999;
              margin-top: 0;
            }

            select {
              margin-left: 10px;
              border: none;
              outline: none;
              text-transform: capitalize;
              color: $dark-font;
            }
          }

          h5 {
            margin-top: 10px;
          }
        }
      }

      .leftside_social {
        position: relative;
        width: 100%;
        padding: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-top: 1px dashed $round-border;

        .footer-social {
          margin-top: 0;

          i {
            font-size: 18px;
          }

          li {
            padding: 10px;
          }
        }

        .call_us {
          h5 {
            text-align: center;

            span {
              display: block;
            }
          }
        }
      }
    }

    .main-menu {
      justify-content: flex-end;

      .brand-logo {
        display: flex;
        align-items: center;
        justify-content: center;
        border-bottom: 1px dashed $round-border;
      }

      .menu-right {
        .header-dropdown {
          > li {
            img {
              width: 20px;
              height: auto;
            }
          }
        }

        .icon-nav {
          .onhover-div {
            > img {
              width: 20px;
              height: auto;
            }
          }
        }
      }

      .menu-left {
        .navbar {
          display: none;
        }

        .mobile-logo {
          display: none;
        }
      }
    }

    .search-overlay {
      padding-left: 300px;
    }

    &.left-header-xl {
      .sidenav {
        width: 350px;
      }

      .top-header {
        padding-left: 350px;
      }
    }

    &.left-header-relative {
      position: sticky;
      top: 50px;
      z-index: 1;
      width: auto;
      box-shadow: none;

      .pixelstrap {
        > li {
          > a {
            text-align: right;
          }

          &:last-child {
            > a {
              padding-bottom: 0;
            }
          }
        }

        a {
          .sub-arrow {
            display: none;
          }
        }
      }

      .sidenav {
        position: relative;
        left: unset;
        top: unset;
        right: unset;
        height: auto;
        width: auto;
        box-shadow: none;
        z-index: 1;

        nav {
          z-index: 1;
          height: auto;
          overflow-y: unset !important;
        }

        .left-sidebar_center {
          padding: 0;
        }
      }

      .main-menu {
        justify-content: flex-end;
      }
    }

    &.left-header-sm {
      width: 240px;
      z-index: 1;

      .sidenav {
        top: 78px;
        z-index: 1;
        width: 240px;
        left: 0;

        .left-sidebar_center {
          padding: 0;
          background-color: $white;

          .pixelstrap {
            a {
              .sub-arrow {
                display: none;
              }
            }

            > li {
              border-bottom: 1px solid #f3f3f3;

              > a {
                padding: 10px 0 20px 0;
                text-align: center;

                img {
                  display: block;
                  margin-left: auto;
                  margin-right: auto !important;
                  width: 48px;
                  border: none;
                }
              }
            }
          }
        }
      }
    }
  }

  &.video-header {
    .top-header {
      background-color: var(--theme-color);

      .header-contact {
        padding: 10px 0;

        li {
          color: $white;
        }
      }

      .header-dropdown {
        li {
          padding: 10px 25px;
          color: $white;

          a {
            i {
              color: $white;
            }
          }
        }
      }
    }
  }

  &.header-christmas {
    position: absolute;
    width: 100%;

    .onhover-div {
      .show-div {
        top: 73px;
      }
    }

    .main-menu {
      .menu-left {
        .navbar {
          padding: 20px 45px 20px 0;

          i {
            color: $white;
          }
        }
      }

      .brand-logo {
        padding-top: 20px;
        padding-bottom: 20px;
      }
    }

    .pixelstrap {
      > li {
        > a {
          padding-top: 20px;
          padding-bottom: 20px;
          color: $white;

          &:hover,
          &:active,
          &:focus,
          &.highlighted {
            padding-top: 20px;
            padding-bottom: 20px;
            color: $white;
          }
        }
      }

      li {
        .lable-nav {
          top: 0;
        }
      }
    }

    .onhover-div {
      padding-top: 20px;
      padding-bottom: 20px;

      > div {
        img {
          filter: brightness(54);
        }
      }
    }

    .top-header {
      z-index: 9;
      position: relative;
      background: transparent;
      border-bottom: 1px solid #d13b3c;

      .header-contact {
        li {
          color: $white;
          font-size: 16px;

          i {
            color: $white;
          }
        }
      }

      .header-dropdown {
        li {
          color: $white;

          a {
            color: $white;

            i {
              color: $white;
            }
          }
        }
      }

      &.blue-layout {
        border-bottom: 1px solid #223b7d;
      }
    }

    .toggle-nav {
      padding-top: 24px;
      padding-bottom: 24px;

      i {
        color: white !important;
      }
    }

    &.sticky {
      box-shadow: none;
      position: relative;
      width: 100%;

      .top-header {
        display: block;
      }
    }
  }

  &.marketplace {
    &.header-tools {
      position: relative;
      width: 100%;
      top: 0;

      &.sticky {
        position: fixed;
      }
    }

    .top-header {
      background-color: var(--theme-color);

      .header-contact {
        li {
          color: $white;
        }
      }

      .header-dropdown {
        li {
          color: $white;

          a {
            color: $white;

            i {
              color: $white;
            }
          }
        }
      }
    }
  }

  &.header-style {
    .top-header {
      .header-dropdown {
        > li {
          padding: 25px 0 25px 20px;
        }
      }
    }

    .onhover-div {
      .show-div {
        top: 53px;
      }
    }

    .onhover-dropdown {
      &:before {
        opacity: 0;
      }
    }
  }
}

.color-dark {
  .top-header {
    background-color: $grey-dark;
  }
}

.hover-unset {
  position: static !important;
}

.sidebar-overlay {
  visibility: hidden;
  opacity: 0;
  background-color: #212331;
  position: fixed;
  z-index: 6;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  transition: all 0.5s ease;
  &.show {
    opacity: 0.8;
    visibility: visible;
    transition: all 0.5s ease;
  }
}

.onhover-dropdown {
  position: relative;
  font-size: 14px;

  .onhover-show-div {
    top: 50px;
    position: absolute;
    z-index: 10;
    background-color: $white;
    transition: all linear 0.3s;
    min-width: 160px;
    text-align: left;
    box-shadow: 0px 1px 2px 0px #cacaca;
    transform: translateY(30px);
    opacity: 0;
    visibility: hidden;
    right: 0;
    padding: 15px 20px;
    border-radius: 0 !important;

    &.product-page-full {
      z-index: 99999;
    }
  }

  &:hover {
    .onhover-show-div {
      opacity: 1;
      transform: translateY(0px);
      visibility: visible;
    }
  }
}

.onhover-div {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  font-family: $font_0, $font_1;

  img,
  i {
    cursor: pointer;
  }

  .show-div {
    top: 103px;
    position: absolute;
    z-index: 9;
    background-color: $white;
    transition: all linear 0.3s;
    min-width: 236px;
    text-align: left;
    transform: translateY(30px);
    opacity: 0;
    visibility: hidden;
    right: 0;
    padding: 0;
    border-radius: 0 !important;
    box-shadow: 0 0 8px $round-border;
  }

  &:hover {
    .show-div {
      opacity: 1;
      transform: translateY(0px);
      visibility: visible;
    }
  }
}

.search-overlay {
  height: 100%;
  width: 100%;
  display: none;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  background-color: lighten($white, 5%);

  > div {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    .overlay-content {
      width: 100%;

      .form-control {
        &:focus {
          box-shadow: none;
        }
      }

      input {
        background-color: transparent;
        border: 0;
        border-bottom: 2px solid lighten($black, 46.5%);
        border-radius: 0;
        padding: 20px 0;

        &::-webkit-input-placeholder {
          /* Chrome/Opera/Safari */
          color: lighten($black, 46.5%);
          font-size: 18px;
        }

        &::-moz-placeholder {
          /* Firefox 19+ */
          color: lighten($black, 46.5%);
          font-size: 18px;
        }

        &:-ms-input-placeholder {
          /* IE 10+ */
          color: lighten($black, 46.5%);
          font-size: 18px;
        }

        &:-moz-placeholder {
          /* Firefox 18- */
          color: lighten($black, 46.5%);
          font-size: 18px;
        }
      }

      button {
        position: absolute;
        top: 10px;
        background-color: transparent;
        border: 0;
        font-size: 24px;
        color: lighten($black, 46.5%);
        right: 10px;
      }
