// sticky header
header {
  &.sticky {
    position: fixed !important;
    width: 100vw;
    top: 0;
    z-index: 9;
    box-shadow: 0 0 5px rgba($black, 0.12);
    transition: all 0.3s ease;
    animation: smoothScroll 0.05s forwards;

    .top-header {
      display: none;
      transition: all 0.3s ease;
    }

    &.header-style-5 {
      &.style-classic {
        .bottom-part {
          display: none;
          transition: all 0.3s ease;
        }
      }
    }
  }

  &.stickycls {
    position: fixed !important;
    width: 100vw;
    top: 0;
    left: 0;
    z-index: 99;
  }
}

@keyframes smoothScroll {
  0% {
    transform: translateY(-20px);
  }

  100% {
    transform: translateY(0px);
  }
}

.left-arrow {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.right-arrow {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}
